.intro {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: 1;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $color-default-black;
    opacity: 0.7;
  }

  .container {
    display: flex;
    justify-content: center;
    min-height: 100vh;
  }

  .intro__scroll {
    position: absolute;
    bottom: 50px;
    display: flex;
    justify-content: center;
    opacity: 0.6;
    transition: opacity $trans-600;

    &::before {
      content: "";
      position: absolute;
      top: 8px;


      height: 7px;
      width: 3px;

      border-radius: 1px;

      background-color: $color-dark-orange;
      transform: translateY(0);
      transition: transform $trans-600;
    }

    svg {
      color: $color-default-white;
      fill: transparent;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        &::before {
          transform: translateY(14px);
        }
      }

      &:active {
        opacity: 0.2;
      }
    }
  }
}

.intro__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 300px;
  width: 900px;


  z-index: 5;

  color: $color-default-white;

  svg {
    color: $color-dark-orange;
  }
}

.intro__typing-text-wrapper {
  display: inline;
  margin-top: 40px;
  text-align: center;
  max-width: 900px;

  span {
    font-family: $ff-second-font;
    font-weight: 700;
    font-size: 56px;
    line-height: 64px;
    text-transform: uppercase;
  }

  .typed-cursor {
    color: $color-dark-orange;
  }
}

.intro__text {
  margin-top: 28px;
  margin-bottom: 0;

  font-size: 24px;
  line-height: 32px;
  color: $color-dark-orange;
  opacity: 0.8;
}

.intro__video {
  position: absolute;
  z-index: 0;
  left: 0;
  top: 0;

  pointer-events: none;
}

.intro__button {
  margin-top: 56px;
}
